import {
	mapState,
	mapActions
} from 'vuex'
import uniSearchBar from '../../components/uni-ui/uni-search-bar/uni-search-bar.vue'
import hotHistory from '../../components/search/hothistory.vue'
export default {
	name: "",
	components: {
		uniSearchBar,
		hotHistory
	},
	props: {},
	data() {
		return {
			// 接收storage中的openId
			openId: '',
			// 输入框的值
			keyword: ''
		}
	},
	methods: {
		...mapActions(['getHistory', 'startSearch', 'addHistorys', 'deleteHistorys']),
		// 搜索框方法
		search() {},
		// 搜索框值变化的方法
		startSearchs(e) {
			this.keyword = e.value
			if (this.keyword.trim() !== '') {
				this.startSearch(this.keyword)
			}
		},
		// 跳转详情页
		goToGoodsDetail(idx) {
			this.addHistorys({
				keyword: this.keyword,
				openId: this.openId
			})
			uni.navigateTo({
				url: `../goodsdetail/goodsdetail?id=${idx}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 子组件分发回父组件的方法
		changeVal(e) {
			this.keyword = e
			this.startSearch(this.keyword)
		},
		// 删除搜索记录
		deletes() {
			uni.showModal({
				content: '确定删除搜索历史吗?',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.deleteHistorys({
							openId: this.openId
						})
						uni.showToast({
							title: '删除成功'
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 取消按钮
		clears() {
			this.keyword = ''
		}
	},
	mounted() {

	},
	onShow() {
		if (uni.getStorageSync('openId')) {
			this.openId = uni.getStorageSync('openId')
			if (this.openId !== '') {
				this.getHistory(this.openId)
			}
		}
	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		...mapState(['historyHotObj', 'searchArr'])
	},
	watch: {

	},
	directives: {

	}
}
